// Enhanced implementation of deviceKey.js with improved cross-browser fingerprinting
const generateDeviceKey = async () => {
    try {
        const array = new Uint8Array(32);
        crypto.getRandomValues(array);
        const key = Array.from(array)
            .map(b => b.toString(16).padStart(2, '0'))
            .join('');
        console.log('Generated new device key:', key.substring(0, 10) + '...');
        return key;
    } catch (err) {
        console.error('Failed to generate device key:', err);
        return null;
    }
};

const getStoredDeviceKey = async () => {
    try {
        console.log('Checking for existing device key');
        
        // Check session storage first
        const sessionKey = sessionStorage.getItem('device_key');
        
        // If we have a key in session storage, use it
        if (sessionKey) {
            console.log('Found existing device key in session storage:', sessionKey.substring(0, 10) + '...');
            return sessionKey;
        }
        
        // No key in session storage, generate a new one
        console.log('No device key found, generating new one');
        const newKey = await generateDeviceKey();
        
        if (newKey) {
            // Store the new key in session storage
            sessionStorage.setItem('device_key', newKey);
            console.log('Stored new device key in session storage');
        }
        
        return newKey;
    } catch (err) {
        console.error('Error in getStoredDeviceKey:', err);
        
        // Fallback: try to use existing key or generate a new one
        const existingKey = sessionStorage.getItem('device_key');
        if (existingKey) {
            return existingKey;
        }
        
        return generateDeviceKey();
    }
};

// UPDATED: Create a more stable installation ID that doesn't rely on screen dimensions
const createInstallationId = () => {
    // Use stable hardware and OS characteristics that remain consistent across browsers
    
    // Platform (Win32, MacIntel, etc.)
    const platform = navigator.platform || 'unknown';
    
    // CPU cores (typically constant across browsers on same device)
    const hardwareConcurrency = navigator.hardwareConcurrency || 'unknown';
    
    // Device memory in GB (typically constant)
    const deviceMemory = navigator.deviceMemory || 'unknown';
    
    // Extract OS version from user agent (stable across browsers on same device)
    const userAgent = navigator.userAgent;
    const osMatches = userAgent.match(/Windows NT (\d+\.\d+)|Mac OS X (\d+[._]\d+)|Android (\d+\.\d+)|iOS (\d+\.\d+)/i);
    const osVersion = osMatches ? (osMatches[1] || osMatches[2] || osMatches[3] || osMatches[4] || '') : 'unknown';
    
    // Extract device model hints from user agent (for mobile)
    const deviceModel = userAgent.match(/\(([^;]+);/i)?.[1] || 'unknown';
    
    // Create hardware identifier from stable components
    const hardwareId = `${platform}_${hardwareConcurrency}_${deviceMemory}_${osVersion}`;
    
    // Generate a device-specific ID
    let deviceUuid = localStorage.getItem('device_uuid');
    if (!deviceUuid) {
        // For first-time use, generate a random identifier
        const array = new Uint8Array(8);
        window.crypto.getRandomValues(array);
        deviceUuid = Array.from(array, byte => 
            byte.toString(16).padStart(2, '0')).join('');
        localStorage.setItem('device_uuid', deviceUuid);
        console.log('Created new device UUID for installation ID:', deviceUuid);
    }
    
    // Include screen info as additional component but not primary identifier
    const screenInfo = `${window.screen.width}x${window.screen.height}x${window.screen.colorDepth}`;
    
    // Store each component separately for component-based matching
    localStorage.setItem('hw_platform', platform);
    localStorage.setItem('hw_cores', hardwareConcurrency.toString());
    localStorage.setItem('hw_memory', deviceMemory.toString());
    localStorage.setItem('hw_osversion', osVersion);
    localStorage.setItem('hw_screeninfo', screenInfo);
    
    // Return combined ID with stable hardware ID first, then UUID
    return `${hardwareId}_${deviceUuid}`;
};

// Generate device fingerprint that is consistent across browsers on same device
const generateDeviceFingerprint = async () => {
    try {
        console.log('Generating device fingerprint...');
        
        // Check for existing installation ID (shared across browsers via localStorage)
        let installationId = localStorage.getItem('installation_id');
        if (!installationId) {
            // Generate a new installation ID
            installationId = createInstallationId();
            localStorage.setItem('installation_id', installationId);
            console.log('Created new installation ID:', installationId);
        } else {
            console.log('Using existing installation ID:', installationId);
        }
        
        // Platform details that are stable across browsers
        const platform = navigator.platform || 'unknown';
        const hardwareConcurrency = navigator.hardwareConcurrency || 'unknown';
        const deviceMemory = navigator.deviceMemory || 'unknown';
        
        // Screen details (may vary between browsers)
        const screenInfo = `${window.screen.width}x${window.screen.height}x${window.screen.colorDepth}`;
        const timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone || 'unknown';
        const language = navigator.language || 'unknown';
        
        // Generate browser-specific components for additional entropy
        const canvasFingerprint = await generateCanvasFingerprint();
        const webglFingerprint = await generateWebGLFingerprint();
        const audioFingerprint = await generateAudioFingerprint();
        
        // Store fingerprint components individually for component-based matching
        const fingerComponents = {
            // Most important for cross-browser matching:
            installationId,
            platform,
            hardwareConcurrency,
            deviceMemory,
            timeZone,
            language,
            
            // Screen info (treated as secondary)
            screenInfo,
            
            // These components may vary between browsers but are still useful:
            userAgent: navigator.userAgent,
            
            // Optional browser-specific components (used with lower weight):
            canvas: canvasFingerprint,
            webgl: webglFingerprint,
            audio: audioFingerprint
        };
        
        // Store individual components in localStorage for component-based matching
        localStorage.setItem('fingerprint_components', JSON.stringify(fingerComponents));
        
        // Generate fingerprint with installation ID as highest priority component
        const priorityComponents = [
            // Installation ID has the highest priority (60% of the fingerprint)
            installationId,
            
            // Hardware components have medium priority (30% of the fingerprint)
            platform,
            hardwareConcurrency.toString(),
            deviceMemory.toString(),
            
            // Other components have lower priority (10% of the fingerprint)
            timeZone,
            language
        ];
        
        // Combine the components into a single string
        const combinedInfo = priorityComponents.join('|');
        
        console.log('Combined fingerprint elements:', 
            combinedInfo.substring(0, 50) + '...');
        
        // Hash the combined information with SHA-256
        const encoder = new TextEncoder();
        const data = encoder.encode(combinedInfo);
        const hashBuffer = await crypto.subtle.digest('SHA-256', data);
        const deviceFingerprint = Array.from(new Uint8Array(hashBuffer))
            .map(b => b.toString(16).padStart(2, '0')).join('');
        
        console.log('Generated device fingerprint:', deviceFingerprint.substring(0, 10) + '...');
        
        // Store the fingerprint and components in localStorage for cross-browser sharing
        localStorage.setItem('device_fingerprint', deviceFingerprint);
        localStorage.setItem('fingerprint_version', '3'); // Updated version
        
        return deviceFingerprint;
    } catch (err) {
        console.error('Error generating fingerprint:', err);
        
        // Fallback to basic fingerprinting if advanced methods fail
        try {
            const basicInfo = `${navigator.platform}|${navigator.hardwareConcurrency}|${navigator.deviceMemory}`;
            console.log('Using basic fingerprint fallback:', basicInfo);
            
            const encoder = new TextEncoder();
            const data = encoder.encode(basicInfo);
            const hashBuffer = await crypto.subtle.digest('SHA-256', data);
            const basicFingerprint = Array.from(new Uint8Array(hashBuffer))
                .map(b => b.toString(16).padStart(2, '0')).join('');
            
            localStorage.setItem('device_fingerprint', basicFingerprint);
            localStorage.setItem('fingerprint_version', '0');
            
            return basicFingerprint;
        } catch (e) {
            console.error('Basic fingerprinting failed:', e);
            return null;
        }
    }
};

// Get fingerprint components for component-based matching
const getFingerprintComponents = () => {
    try {
        const componentsString = localStorage.getItem('fingerprint_components');
        if (componentsString) {
            const components = JSON.parse(componentsString);
            
            // DEBUG: Log the installation ID to help troubleshoot
            if (components && components.installationId) {
                console.log('Sending installation ID:', components.installationId);
            } else {
                console.warn('No installation ID found in components');
            }
            
            return components;
        }
    } catch (e) {
        console.error('Error parsing fingerprint components:', e);
    }
    return null;
};

// Canvas fingerprinting (for additional entropy)
const generateCanvasFingerprint = async () => {
    try {
        const canvas = document.createElement('canvas');
        canvas.width = 200;
        canvas.height = 50;
        const ctx = canvas.getContext('2d');
        
        // Text with special Unicode characters
        ctx.textBaseline = 'top';
        ctx.font = '16px Arial';
        ctx.fillStyle = '#F60';
        ctx.fillRect(125, 10, 50, 30);
        ctx.fillStyle = '#069';
        ctx.fillText('SuperApp 👋🌍', 4, 4);
        
        // More drawing operations for uniqueness
        ctx.globalCompositeOperation = 'multiply';
        ctx.fillStyle = 'rgb(255,0,255)';
        ctx.beginPath();
        ctx.arc(50, 25, 15, 0, Math.PI * 2, true);
        ctx.closePath();
        ctx.fill();
        
        // Get data URL and extract part of it for fingerprint
        const dataURL = canvas.toDataURL();
        return dataURL.substr(-64);
    } catch (e) {
        console.warn('Canvas fingerprinting not supported:', e);
        return 'canvas-not-supported';
    }
};

// WebGL fingerprinting (for additional entropy)
const generateWebGLFingerprint = async () => {
    try {
        const canvas = document.createElement('canvas');
        const gl = canvas.getContext('webgl') || 
                  canvas.getContext('experimental-webgl');
        
        if (!gl) return 'webgl-not-supported';
        
        // Collect WebGL attributes
        const debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
        const vendor = gl.getParameter(debugInfo ? 
                      debugInfo.UNMASKED_VENDOR_WEBGL : gl.VENDOR);
        const renderer = gl.getParameter(debugInfo ? 
                       debugInfo.UNMASKED_RENDERER_WEBGL : gl.RENDERER);
        
        // Combine with other WebGL parameters
        const paramNames = ['ALPHA_BITS', 'BLUE_BITS', 'GREEN_BITS', 'RED_BITS', 'DEPTH_BITS',
                          'MAX_VERTEX_ATTRIBS', 'MAX_TEXTURE_SIZE'];
        const params = paramNames.map(name => {
            try {
                return `${name}:${gl.getParameter(gl[name])}`;
            } catch (e) {
                return `${name}:null`;
            }
        }).join('|');
        
        return `${vendor}|${renderer}|${params}`;
    } catch (e) {
        console.warn('WebGL fingerprinting not supported:', e);
        return 'webgl-error';
    }
};

// Audio fingerprinting (for additional entropy)
const generateAudioFingerprint = async () => {
    try {
        if (typeof AudioContext === 'undefined' && 
            typeof webkitAudioContext === 'undefined') {
            return 'audio-not-supported';
        }
        
        const audioContext = new (window.AudioContext || window.webkitAudioContext)();
        const analyser = audioContext.createAnalyser();
        const oscillator = audioContext.createOscillator();
        
        // Setup and connect nodes
        analyser.fftSize = 64;
        oscillator.type = 'triangle';
        oscillator.connect(analyser);
        
        // Start the oscillator for a brief moment
        oscillator.start(0);
        setTimeout(() => oscillator.stop(), 50);
        
        // Capture audio data
        const data = new Uint8Array(analyser.frequencyBinCount);
        analyser.getByteTimeDomainData(data);
        
        // Close the audio context
        if (audioContext.close) {
            try {
                audioContext.close();
            } catch (e) {
                console.warn('Error closing audio context:', e);
            }
        }
        
        // Create a fingerprint from the data
        return Array.from(data).join(',');
    } catch (e) {
        console.warn('Audio fingerprinting not supported:', e);
        return 'audio-not-supported';
    }
};

// Get device fingerprint (create if not exists)
const getDeviceFingerprint = async () => {
    try {
        // Check for existing fingerprint first
        const existingFingerprint = localStorage.getItem('device_fingerprint');
        if (existingFingerprint) {
            console.log('Using existing device fingerprint:', 
                existingFingerprint.substring(0, 10) + '...');
            return existingFingerprint;
        }
        
        // Generate new fingerprint if none exists
        return generateDeviceFingerprint();
    } catch (err) {
        console.error('Error retrieving/generating device fingerprint:', err);
        return null;
    }
};

const storeDeviceKey = (key) => {
    if (key) {
        sessionStorage.setItem('device_key', key);
        console.log('Stored device key:', key.substring(0, 10) + '...');
    } else {
        console.warn('Attempted to store null or undefined device key');
    }
};

// Store device session data from API response
const storeDeviceSessionData = (data) => {
    if (!data) return;
    
    console.log('Storing device session data:', data);
    
    // Store device key if provided
    if (data.device_key) {
        storeDeviceKey(data.device_key);
    }
    
    // Store authentication state
    if (data.status === 'authenticated') {
        sessionStorage.setItem('device_session', 'authenticated');
    }
    
    // Store user data
    if (data.handle) {
        sessionStorage.setItem('current_handle', data.handle);
    }
    
    if (data.phone) {
        sessionStorage.setItem('current_phone', data.phone);
    }
    
    if (data.guid) {
        sessionStorage.setItem('device_guid', data.guid);
    }
    
    if (data.masked_phone) {
        sessionStorage.setItem('masked_phone', data.masked_phone);
    }
    
    // Set timestamp for cache control
    sessionStorage.setItem('last_device_check', Date.now().toString());
};

// Clear device session data but preserve the key for future recognition
const clearDeviceSession = () => {
    // Save the handle and device key before clearing
    const deviceKey = sessionStorage.getItem('device_key');
    const currentHandle = sessionStorage.getItem('current_handle');
    
    // Store the handle before logout for future cross-device recognition
    if (currentHandle) {
        localStorage.setItem('previous_handle', currentHandle);
    }
    
    // Flag logout state in localStorage (persists across page reloads)
    localStorage.setItem('logout_state', 'true');
    
    // Clear session data
    sessionStorage.clear();
    
    // Restore just the device key for future device recognition
    if (deviceKey) {
        sessionStorage.setItem('device_key', deviceKey);
    }
    
    // Mark logging out in session
    sessionStorage.setItem('logging_out', 'true');
    
    console.log('Cleared device session data, preserved device key for recognition');
};

module.exports = {
    generateDeviceKey,
    getStoredDeviceKey,
    storeDeviceKey,
    storeDeviceSessionData,
    clearDeviceSession,
    // Export fingerprinting functions
    generateDeviceFingerprint,
    getDeviceFingerprint,
    // New exports for component-based matching
    getFingerprintComponents
};
