// app/javascript/config/axios.js
import axios from 'axios';
import { getStoredDeviceKey, getDeviceFingerprint, getFingerprintComponents } from '../utils/deviceKey';

const instance = axios.create({
  baseURL: `${window.location.protocol}//${window.location.host}/api/v1/auth`,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  }
});

instance.interceptors.request.use(async config => {
  try {
    // Get browser key from session storage
    const browserKey = await getStoredDeviceKey();
    if (browserKey) {
      console.log('Setting request header X-Device-Key:', browserKey.substring(0, 10) + '...');
      config.headers['X-Device-Key'] = browserKey;
    }
    
    // Get device fingerprint and include in headers
    const deviceFingerprint = await getDeviceFingerprint();
    if (deviceFingerprint) {
      console.log('Setting request header X-Device-Fingerprint:', deviceFingerprint.substring(0, 10) + '...');
      config.headers['X-Device-Fingerprint'] = deviceFingerprint;
    }
    
    // NEW: Add fingerprint components for cross-browser recognition
    const fingerprintComponents = getFingerprintComponents();
    if (fingerprintComponents) {
      console.log('Setting request header X-Fingerprint-Components with installation ID:', 
        fingerprintComponents.installationId ? fingerprintComponents.installationId.substring(0, 10) + '...' : 'missing');
      config.headers['X-Fingerprint-Components'] = JSON.stringify(fingerprintComponents);
    }
    
    // Add CSRF token
    const csrfToken = document.querySelector('meta[name="csrf-token"]')?.content;
    if (csrfToken) {
      config.headers['X-CSRF-Token'] = csrfToken;
    }
    
    return config;
  } catch (error) {
    console.error('Request interceptor error:', error);
    return config;
  }
});

// Response interceptor to store device session data
instance.interceptors.response.use(response => {
  if (response.data) {
    if (response.data.device_key) {
      sessionStorage.setItem('device_key', response.data.device_key);
    }
    
    if (response.data.status === 'authenticated') {
      sessionStorage.setItem('device_session', 'authenticated');
      if (response.data.handle) {
        sessionStorage.setItem('current_handle', response.data.handle);
      }
    }
  }
  
  return response;
});

export default instance;
